:root {
  --color-black: #000;
  --color-white: #fff;
  --white: #ffffff;
  --grey-lighten: #e9e9e9;
  --grey-light: #bdbdbd;
  --grey: #8a8a8a;
  --grey-dark: #7b7b7b;
  --black: #222222;
  --black-transparent: rgba(0 0 0 / 70%);
  --pink: #fff0f4;
  --purple: #970e5f;
  --red: #da2852;
  --font-sans:
    "Onest", system-ui, Inter, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --body-bg: var(--back-primary);
  --body-color: var(--front-primary);
  --font-base: var(--font-sans);
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --front-primary: var(--black);
  --front-secondary: var(--grey-dark);
  --front-inverted: var(--white);
  --back-primary: var(--pink);
  --back-secondary: var(--white);
  --back-tertiary: var(--black);
  --darker-bg-color: var(--black-transparent);
  --link-hover-color: var(--red);
  --link-active-color: var(--purple);
  --input-focus-color: var(--purple);
  --brand-gradient: linear-gradient(85deg, var(--purple) 0%, var(--red) 100%);
  --block-gradient: linear-gradient(90deg, #fef0e1 0%, #ffe6cb 100%);
  --scrollbar-bg: #cdcecf;
  --transition-fast: 0.125s ease;
  --transition-default: 0.25s ease;
  --transition-slow: 0.75s ease;
  --transition-slower: 1.5s ease;
  --shadow-dropmenu: 0 4px 14px 0 rgba(0, 0, 0, 0.24);
  --shadow-select-drop: 0 4px 14px 0 rgba(0, 0, 0, 0.24);
  --button-shadow: 0px 4px 45px 0px rgba(0, 0, 0, 0.25);
  --z-index-dropnav: 4;
  --z-index-header: 3;
  --z-index-darker: 2;
  --z-index-base: 1;
  --max-width: 1296px;
  --page-padding: 0;
  --header-height: 90px;
}
@media screen and (min-width: 768px) {
  :root {
    --page-padding: 0;
  }
}
@media screen and (min-width: 1280px) {
  :root {
    --page-padding: 52px;
  }
}
@media screen and (min-width: 768px) {
  :root {
    --header-height: 98px;
  }
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}
body[data-state=nav-open] {
  overflow: hidden;
}
@media screen and (min-width: 1280px) {
  body[data-state=nav-open] {
    overflow: auto;
  }
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

.hystmodal__opened {
  overflow: hidden;
}

.hystmodal__shadow {
  background: var(--back-transparency);
  border: none;
  bottom: 0;
  display: block;
  left: 0;
  opacity: 0;
  overflow: hidden;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  transition: opacity 0.25s ease;
  width: 100%;
  z-index: 98;
}

.hystmodal__shadow--show {
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.25s;
}

.hystmodal {
  bottom: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  left: 0;
  opacity: 1;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 99;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--moved,
.hystmodal--active {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  margin: auto;
  min-height: 100%;
  width: 100%;
  background-color: var(--darker-bg-color);
  backdrop-filter: blur(2px);
}

.hystmodal__window {
  box-sizing: border-box;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 5rem 0;
  max-width: 100%;
  opacity: 0;
  overflow: visible;
  transform: scale(0.9);
  transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
}

.hystmodal--active .hystmodal__window {
  opacity: 1;
  transform: scale(1);
}

.hystmodal__close {
  background: transparent;
  border: none;
  cursor: pointer;
  display: block;
  font-size: 0;
  outline: none;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
}

